.card {
  background-color: black;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.card-body {
  width: 30rem;
  min-height: 30vh;
  background-color: hsl(216deg, 12%, 8%);
  padding: 3rem;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  position: relative;
  z-index: 2;
}
.card-body img.star {
  width: 1.3rem;
  height: 1.3rem;
  background-color: hsl(213deg, 19%, 18%);
  border-radius: 50%;
  padding: 1.3rem 1.3rem;
}
.card-body ul {
  list-style: none;
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.card-body ul li a {
  text-decoration: none;
  font-size: 1.5rem;
  color: hsl(216deg, 12%, 54%);
  display: block;
  background-color: hsl(213deg, 19%, 18%);
  border-radius: 100%;
  padding: 1rem 1.5rem;
  text-align: center;
}
.card-body ul li a:hover {
  color: hsl(0deg, 0%, 100%);
  background-color: hsl(25deg, 97%, 53%);
  border-radius: 100%;
  padding: 1rem 1.5rem;
}
.card-body ul li .rate {
  background-color: hsl(217deg, 12%, 63%);
  color: hsl(0deg, 0%, 100%);
}
.card-body button {
  align-self: center;
  border: none;
  background-color: hsl(25deg, 97%, 53%);
  border-radius: 5rem;
  padding: 1rem 12rem;
}
.card-body button:hover {
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 5rem;
  padding: 1rem 12rem;
}
.card-body button a {
  text-decoration: none;
  color: hsl(0deg, 0%, 100%);
}
.card-body2 {
  align-items: center;
  position: absolute;
  z-index: 1;
  opacity: 0;
}
.card-body2 img.feed-back {
  width: 20rem;
  height: 13rem;
}
.card-body2 #rated {
  width: 20rem;
  font-size: 15px;
  color: hsl(0deg, 0%, 100%);
  background-color: hsl(213deg, 19%, 18%);
  border-radius: 5rem;
  padding: 1rem 0rem;
  text-align: center;
}
.card-texts2 {
  text-align: center;
}
.card .show {
  z-index: 5;
  opacity: 1;
}

p {
  font-size: 15px;
  font-weight: 400;
}

h1 {
  font-size: 20px;
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
  margin-bottom: 10px;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

html {
  font-size: 62.5%;
  color: hsl(217deg, 12%, 63%);
  font-family: "https://fonts.google.com/specimen/Overpass";
}

::selection {
  color: hsl(216deg, 12%, 54%);
}